.main-page-second-content-block{
  background: #254A5A;
  margin-bottom: 125px;
  max-width: 1541px;
  width: 100%;
  margin: 0px auto 130px;
}

.main-page-second-content-block__wrapper{
  margin: auto;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 1050px;
  padding: 142px 0px 147px;
  position: relative;
}

.main-page-second-content-block__img{
  position: absolute;
  top: 15%;
  left: 55%;

  img{
    width: 100%;
  }
}
.main-page-second-content-block__svg{
  position: absolute;
  top: -3.5%;
  right: 37%;

  svg{
    width: 100%;
  }
}

@media (max-width: 768px) {
  .main-page-second-content-block__img{
    display: none;
  }

  .main-page-second-content-block__wrapper{
    align-items: center;
    justify-content: center;
  }

  .main-page-second-content-block{
    margin: 0 auto;
  }

  .main-page-second-content-block__svg{
    right: 3%;
  }
}
